  data () {
    return {
      array: ['病假', '事假', '年假'],
      textValue: 0,
      startdate: '2018-01-01',
      enddate: '2018-05-08',
      starttime: '09:00',
      endtime: '09:00'
    }
  },
  methods: {
    //请假方式
    textChange (evt) {
      console.log(evt.detail.value)
      this.textValue = evt.detail.value;

    },
    startdateChange (evt) {
      this.startdate = evt.detail.value;

    },
    starttimeChange (evt) {
      this.starttime = evt.detail.value;
    },
    //结束日期
    endtimeChange (evt) {
      this.endtime = evt.detail.value;
    },
    enddateChange (evt) {
      this.enddate = evt.detail.value;
    }
  }
}



